html {
  overflow-x: hidden;
  max-width: 100vw;

  &.overflowYStop {
    overflow-y: hidden;
  }

  &.overflowYScroll {
    overflow-y: scroll;
  }

  @include respond(tabland) {

    &.overflowYStop-min {
      overflow-y: hidden;
    }
  }

}


.homepage {
  font-size: 15px;
  overflow-x: hidden;
  font-family: $font-1;
  max-width: 100%;

  *::-moz-selection,
  *::selection {
    color: #fff;
    background-color: $color-main;
  }


  a {
    color: inherit;
    text-decoration: none;
    outline: none;
    transition: all 400ms ease;
    -moz-transition: all 400ms ease;
    -webkit-transition: all 400ms ease;
    -ms-transition: all 400ms ease;
    -o-transition: all 400ms ease;

    &:hover {
      color: $color-main;
      text-decoration: none;
      outline: none;
    }

    &:focus {
      outline: none;
    }
  }

  p {
    font-family: $font-1;
    font-size: 15px;
    font-weight: 400;

    &.p {
      &-large {
        font-size: 1.8rem;
        line-height: 1.8;
      }
    }

    i {
      font-weight: 600;
    }

    @include respond(tabland) {
      font-size: 1.8rem;
      line-height: 1.5;
    }
  }

  .wow {
    animation-delay: 0.5s;
  }

  .container.row {
    margin: 0 auto;
  }

  .container {
    @include respond(tabland-min) {
      max-width: 990px;
    }

    @include respond(smdesktop-min) {
      max-width: 1200px;
    }

    @include respond(bgdesktop-min) {
      max-width: 1300px;
    }

    @media (min-width:1600px) {
      max-width: 1450px;
    }

    @media (min-width: 2000px) {
      max-width: 1680px;
    }
  }

  .hide,
  .hidden {
    display: none !important;
  }

  i.fa-phone {
    transform: rotate(90deg);
  }

  .sitecontent {
    // min-height: 100vh;
  }

  .box {
    padding: 3rem;
    background-color: rgba(#000, 0.8);
    margin-bottom: 3rem;

    &-inner {
      background-color: rgba(#fff, 0.15);
      padding: 2rem;
    }
  }

  .section {
    position: relative;
    padding: 70px 0;
    overflow: hidden;

    &-bg {
      @include absoldefault;

      &::after {
        content: "";
        @include absoldefault;
        background: rgba(#000, 0.7);
      }
    }

    &-head {
      margin-bottom: 4rem;
    }

    @include respond(tabland) {
      padding: 4rem 0;

      &-head {
        margin-bottom: 2rem;
      }
    }
  }

  .post {
    &-content {
      .heading {
        font-size: 4rem;

        &-mini {
          margin-top: 2rem;
        }
      }

      .list-content {
        li {
          font-size: 1.75rem;

          &::before {
            top: 0.3rem;
          }
        }
      }
    }
  }

  .ui-timepicker-container {
    z-index: 9 !important;

    .ui-menu-item {
      a {
        font-family: $font-1;
        color: #fff;
        background-color: $color-sub;

        &:hover {
          color: #fff;
          background-color: $color-main;
        }
      }
    }
  }

  .slick-slide {

    &,
    &:focus {
      outline: none;
    }
  }

  .spacer {
    width: 2px;
    height: 14px;
    background-color: #000;
    display: inline-block;
    margin-left: 20px;
    margin-right: 20px;
  }
}